import React, { Component } from "react";
import { BrowserRouter, HashRouter, Switch, Redirect, Route } from "react-router-dom";
import PrivateRoute from "Extended/privateRoute";

import Home from "./Home/Home";
import Nomatch from "./Nomatch/Nomatch";
import Ucenter from "./Ucenter/Ucenter";

import asyncComponents from "Extended/asyncComponents";
const Login = asyncComponents(() => import("./Login/Login"), true);
const Register = asyncComponents(() => import("./Register/Register"), true);

const RootRoutes = () => {
    let baseRouterUrl = AppConfig.baseRouterUrl;
    let homeRouterUrl = AppConfig.homeRouterUrl || "/";
    // let baseRouterUrl = "/";
    // let homeRouterUrl = "/";
    return (
        <HashRouter>
            <Switch>
                <Route path={baseRouterUrl + "login"} component={Login} />
                <Route path={baseRouterUrl + "register"} component={Register} />
                <PrivateRoute path={baseRouterUrl + "ucenter"} exact component={Ucenter} />
                <Route path={homeRouterUrl} exact component={Home} />
                <Route path="*" component={Nomatch} />
            </Switch>
        </HashRouter>
    );
};

export default RootRoutes;
