import React, { Component } from "react";

import LoadingFarmeWork from "Components/Layout/LoadingFarmeWork";
import Loading from "Components/Layout/Loading";
import RootRoutes from "./pages/RootRoutes";

import delay from "Utils/delay";

import { contextConsumers } from "Libs/ContextRudex";
@contextConsumers()
export default class InitRouter extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: true,
            error: false,
            isInit: false
        };
    }
    componentDidMount() {
        this.getInit();
    }
    getInit = async () => {
        if (isweixin) {
            this.InitWeixin();
        } else {
            this.InitApp();
        }
    };
    InitWeixin = async () => {
        if (weixinuserinfo.nickname) {
            this.props.dispatch.set({
                authorization: openid,
                user: weixinuserinfo
            });
        }
        this.setState({
            isInit: true
        });
    };
    InitApp = async () => {
        if (localStorage.token) {
            this.getInitUser();
        } else {
            this.setState({
                isInit: true
            });
        }
        // await delay(1000);
    };
    getInitUser = async () => {
        if (this.loading) {
            return;
        }
        this.loading = true;
        this.setState({
            loading: true,
            error: false
        });
        let res = await Request("user/query", {
            params: {
                token: localStorage.token,
                id: localStorage.userId
            }
        });
        this.loading = false;
        if (res.success) {
            this.props.dispatch.set({
                token: localStorage.token,
                user: res.data.user
            });
            this.setState({
                isInit: true,
                loading: false
            });
        } else {
            this.setState({
                loading: false,
                error: true
            });
        }
    };
    render() {
        let { isInit, error, loading } = this.state;
        if (!isInit) {
            return (
                <LoadingFarmeWork>
                    <Loading
                        loading={loading}
                        loadingTitle="初始化中..."
                        error={error}
                        errorAction={<div onClick={this.getInit}>初始化失败，点击重试</div>}
                    />
                </LoadingFarmeWork>
            );
        }
        return <RootRoutes />;
    }
}
